.Portfolio {
	display: flex;
	align-items: center;
	flex-direction: column;

	margin-left: 4rem;
	margin-right: 4rem;

	width: 100%;
	max-width: 900px;
	font-size: 1.2rem;
}

.portfolio-intro {

	margin-top: 7rem;
	padding-bottom: 5rem;

	line-height: 1.7rem;
	font-size: 1rem;
	font-weight: 300;

	border-bottom: solid 1px rgb(227, 227, 227);

	span {
		text-decoration: underline;
		cursor: pointer;
	}
}

.empty-space {
	height: 3rem;
}